import { getToken, setToken, removeToken } from '../../utils/auth'
import { login, changePassword } from '../../services/auth'

const state = {
	token: getToken(),
	user: {}
}

const mutations = {
	setToken: (state, token) => {
		state.token = token
	},
	setUSer: (state, user) => {
		state.user = user
	},
	clearUser: (state) => {
		state.user = {}
	},
	clearToken: (state) => {
		state.token = ''
	}
}

const actions = {
	login({ commit }, loginForm) {
		return new Promise((resolve, reject) => {
			login(loginForm).then(res => {
				const { data } = res

				commit('setToken', data.token)
				commit('setUSer', data.user)
				setToken(data.token)
				
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	changePassword({ commit }, passwordForm) {
		return new Promise((resolve, reject) => {
			changePassword(passwordForm).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	logout({ commit }) {
		return new Promise((resolve) => {
			removeToken()
			commit('clearUser')
			commit('clearToken')
			resolve()
		})
	}
}

const getters = {}

export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations
}