import { fetch, add, edit, deleteById, resetPassword } from '../../services/user'

const state = {
	list: [],
	total: 0,
	companies: []
}

const mutations = {
	setList: (state, list) => {
		state.list = list
	},
	setTotal: (state, total) => {
		state.total = total
	},
	setCompanies: (state, companies) => {
		state.companies = companies
	}
}

const actions = {
	fetch({ commit }, searchForm) {
		return new Promise((resolve, reject) => {
			fetch(searchForm).then(res => {
				const { data } = res

				commit('setList', data.list)
				commit('setTotal', data.total)
				commit('setCompanies', data.companies)
				
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	add({ commit }, data) {
		return new Promise((resolve, reject) => {
			add(data).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	edit({ commit }, data) {
		return new Promise((resolve, reject) => {
			edit(data).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	deleteById({ commit }, id) {
		return new Promise((resolve, reject) => {
			deleteById(id).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	resetPassword({ commit }, id) {
		return new Promise((resolve, reject) => {
			resetPassword(id).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	}
}

const getters = {}

export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations
}