import apiCall from './api'

export function fetch(data) {
	return apiCall({
		url: '/company',
		method: 'get',
		params: data
	})
}

export function add(data) {
	return apiCall({
		url: '/company',
		method: 'post',
		data
	})
}

export function edit(data) {
	return apiCall({
		url: '/company/' + data._id,
		method: 'put',
		data
	})
}

export function deleteById(id) {
	return apiCall({
		url: '/company/' + id,
		method: 'delete'
	})
}