import apiCall from './api'

export function fetchWarehouses() {
	return apiCall({
		url: '/calendar/warehouse',
		method: 'get'
	})
}

export function fetch(data) {
	return apiCall({
		url: '/calendar',
		method: 'get',
		params: data
	})
}

export function booking(data) {
	return apiCall({
		url: '/calendar',
		method: 'post',
		data,
		headers: { "Content-Type": "multipart/form-data" },
	})
}

export function download(attachment) {
	return apiCall({
		url: '/calendar/download/' + attachment,
		method: 'get',
		responseType: 'arraybuffer'
	})
}

export function deleteById(id) {
	return apiCall({
		url: '/calendar/' + id,
		method: 'delete'
	})
}

export function move(data) {
	return apiCall({
		url: '/calendar/' + data.id,
		method: 'put',
		data
	})
}