import { fetch, fetchWarehouses, booking, download, deleteById, move } from '../../services/calendar'
import moment from 'moment'
import { hourMinute } from '../../utils/helper'

const state = {
	pallets: [],
	containers: [],
	warehouses: [],
	list: [],
	total: 0
}

const mutations = {
	setContainers: (state, containers) => {
		state.containers = containers
	},
	setPallets: (state, pallets) => {
		state.pallets = pallets
	},
	setWarehouses: (state, warehouses) => {
		state.warehouses = warehouses
	},
	setList: (state, list) => {
		state.list = list
	},
	setTotal: (state, total) => {
		state.total = total
	}
}

const actions = {
	fetch({ commit }, searchForm) {
		return new Promise((resolve, reject) => {
			fetch(searchForm).then(res => {
				const { data } = res

				let containers = []
				let pallets = []

				for (let i = 0; i <= 6; i++) {
					let date = moment(data.startDate).add(i, 'day').hours(0).minutes(0).seconds(0).milliseconds(0).toDate()
					data.slots.forEach(slot => {
						if (date >= new Date(slot.fromDate) && date <= new Date(slot.toDate)) {
							
							slot.fromTime.hour = slot.fromTime.hour + ''
							slot.fromTime.minute = slot.fromTime.minute + ''

							slot.toTime.hour = slot.toTime.hour + ''
							slot.toTime.minute = slot.toTime.minute + ''

							let isClosed = new Date() >= moment(date).hours(slot.toTime.hour).minutes(slot.toTime.minute).seconds(0).milliseconds(0)
							let freeCapacity = slot.capacity

							data.slotBookings.forEach(booking => {
								if (booking.slot == slot._id) {
									if (moment(booking.date).hours(0).minutes(0).seconds(0).milliseconds(0).toDate().getTime() == moment(date).hours(0).minutes(0).seconds(0).milliseconds(0).toDate().getTime()) {
										freeCapacity -= booking.slotQty
									}
								}
							})

							let newSlot = {
								_id: slot._id,
								date,
								start: moment(date).format('YYYY-MM-DD') + ' ' + hourMinute(slot.fromTime.hour) + ':' + hourMinute(slot.fromTime.minute),
								end: moment(date).format('YYYY-MM-DD') + ' ' + hourMinute(slot.toTime.hour) + ':' + hourMinute(slot.toTime.minute),
								freeCapacity,
								isClosed,
								class: slot.loadUnit,
							}

							if (slot.loadUnit == 'pallet') {
								pallets.push(newSlot)
							}
							else {
								containers.push(newSlot)
							}
						}
					})
				}

				commit('setContainers', containers)
				commit('setPallets', pallets)

				commit('setList', data.list)
				commit('setTotal', data.total)

				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},

	fetchWarehouses({ commit }) {
		return new Promise((resolve, reject) => {
			fetchWarehouses().then(res => {
				const { data } = res
				commit('setWarehouses', data.warehouses)
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},

	// eslint-disable-next-line no-unused-vars
	booking({ commit }, data) {
		return new Promise((resolve, reject) => {
			booking(data).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		}) 
	},

	// eslint-disable-next-line no-unused-vars
	download({ commit }, attachment) {
		return new Promise((resolve, reject) => {
			download(attachment).then(res => {				
				resolve(res)
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	deleteById({ commit }, id) {
		return new Promise((resolve, reject) => {
			deleteById(id).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	move({ commit }, data) {
		return new Promise((resolve, reject) => {
			move(data).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
}

const getters = {}

export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations
}