import apiCall from './api'

export function fetch(data) {
	return apiCall({
		url: '/user',
		method: 'get',
		params: data
	})
}

export function add(data) {
	return apiCall({
		url: '/user',
		method: 'post',
		data
	})
}

export function edit(data) {
	return apiCall({
		url: '/user/' + data._id,
		method: 'put',
		data
	})
}

export function deleteById(id) {
	return apiCall({
		url: '/user/' + id,
		method: 'delete'
	})
}

export function resetPassword(id) {
	return apiCall({
		url: '/user/password/' + id,
		method: 'post'
	})
}