import { fetch, add, edit, deleteById } from '../../services/slot'

const state = {
	warehouse: {},
	list: [],
	total: 0
}

const mutations = {
	SET_WAREHOUSE: (state, warehouse) => {
		state.warehouse = warehouse
	},
	SET_LIST: (state, list) => {
		state.list = list
	},
	SET_TOTAL: (state, total) => {
		state.total = total
	}
}

const actions = {
	fetch({ commit }, data) {
		return new Promise((resolve, reject) => {
			fetch(data).then(res => {
				const { data } = res

				commit('SET_LIST', data.list)
				commit('SET_TOTAL', data.total)
				commit('SET_WAREHOUSE', data.warehouse)

				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	add({ commit }, data) {
		return new Promise((resolve, reject) => {
			add(data).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	edit({ commit }, data) {
		return new Promise((resolve, reject) => {
			edit(data).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
	// eslint-disable-next-line no-unused-vars
	deleteById({ commit }, id) {
		return new Promise((resolve, reject) => {
			deleteById(id).then(() => {
				resolve()
			}).catch(err => {
				reject(err)
			})
		})
	},
}

const getters = {}

export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations
}